.sidebar {
    height: auto;
    width: 100%;
    top: 0;
    left: 0;
    background-color: rgb(253, 251, 251);
    overflow: auto;
    overflow-y: scroll;
  }
  .popup {  
    position: fixed;  
    width: 100%;  
    height: 100%;  
    top: 0;  
    left: 0;  
    right: 0;  
    bottom: 0;  
    margin: auto;  
    background-color: rgba(0,0,0, 0.5);  
  }  
  .popup\inner {  
    position: absolute;  
    left: 25%;  
    right: 25%;  
    top: 25%;  
    bottom: 25%;  
    margin: auto;  
    border-radius: 20px;  
    background: white;  
  }
  .btn {
    position: relative;
    float: right;
  }